
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  color: #333;
  font-size: 15px;
  overflow: hidden;
  background-color: #fff;
}
#app .editor-wrapper {
  height: 100%;
}
#app .editor-wrapper .editor-content {
  width: 800px;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
  background-color: #fff;
  padding: 70px 40px 30px;
  box-shadow: 0 1px 6px #999;
}
#app .editor-wrapper .editor-content .editor {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  outline: none;
}



